import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ContactPage } from '../contact/contact';
import { MapaPage } from '../mapa/mapa';
import { EncuestaPage } from '../encuesta/encuesta';
/*
  Generated class for the Admin page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
import { Http, Headers, RequestOptions /*Response*/ } from '@angular/http';


import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Component({
  selector: 'page-inicio',
  templateUrl: 'inicio.html'
})
export class InicioPage {

  data = new Array<Object>();
  constructor(public navCtrl: NavController,
    private http: Http) {}

  ionViewDidLoad() {
    console.log('Pagina de Inicio');
  }

	contact = ():void=>{
	  this.navCtrl.push(MapaPage);
	}

  mapa = ():void=>{
    this.navCtrl.push(MapaPage);
  }

  convenios = ():void=>{

    var url = 'http://vc.solnet.cl/rest/api/get/Convenios';

    

    this.http.get(url)
    .map(res => res.json())
    .subscribe(data => {
      this.data = data.map( (d) => {
       
        return d;
      });
      
      
        //this.navCtrl.push(HomePage, {data:this.data});
        console.log({data:this.data});
        this.navCtrl.push(ContactPage, {data:this.data});
      
    });
      
  }
    encuestas = ():void=>{

    var url = 'http://vc.solnet.cl/rest/api/get/Encuestas';

    

    this.http.get(url)
    .map(res => res.json())
    .subscribe(data => {
      this.data = data.map( (d) => {
       
        return d;
      });
      
      
        //this.navCtrl.push(HomePage, {data:this.data});
        console.log({data:this.data});
        this.navCtrl.push(EncuestaPage, {data:this.data});
      
    });
    


    //this.navCtrl.push(ContactPage);
  }

}



